body {
  background-color: #121212;
  color: #ffffff;
  margin: 0;
  font-family: Arial, sans-serif;
}

.App {
  text-align: center;
  font-family: Arial, sans-serif;
  padding: 20px;
}

.start-screen, .player-role-screen, .score-screen, .board-screen, .reveal-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 20px;
}

h1 {
  font-size: 4em;
  margin-bottom: 20px;
}

h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
}

.player-slider, .score-list, .board, .reveal-list {
  margin-top: 20px;
}

.player-slider label {
  font-size: 1.5em;
}

.player-slider input[type=range] {
  width: 80%;
  margin-top: 10px;
}

.board {
  display: inline-block;
  margin: 20px;
}

.board-row {
  display: flex;
  flex-wrap: wrap;
}

.board-cell {
  width: 100px;
  height: 40px;
  margin: 5px;
  background-color: #333;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  font-size: 14px;
  color: #ffffff;
  transition: background-color 0.3s, transform 0.3s;
}

.board-cell:hover {
  background-color: #444;
  transform: scale(1.05);
}

button {
  margin: 10px;
  padding: 15px 30px;
  font-size: 1.5em;
  border-radius: 8px;
  background-color: #1e88e5;
  border: none;
  cursor: pointer;
  color: #ffffff;
  transition: background-color 0.3s, transform 0.3s;
}

button:hover {
  background-color: #1565c0;
  transform: scale(1.05);
}

.hidden {
  display: none;
}

@media (max-width: 600px) {
  h1 {
    font-size: 2.5em;
  }

  h2 {
    font-size: 2em;
  }

  .board-cell {
    width: 80px;
    height: 30px;
    font-size: 12px;
  }

  button {
    padding: 10px 20px;
    font-size: 1.2em;
  }
  
  .player-slider input[type=range] {
    width: 100%;
  }
}

@media (max-width: 400px) {
  h1 {
    font-size: 2em;
  }

  h2 {
    font-size: 1.5em;
  }

  .board-cell {
    width: 70px;
    height: 25px;
    font-size: 10px;
  }

  button {
    padding: 8px 16px;
    font-size: 1em;
  }
}
